import {Vector} from "./vector"

export class CubeState{
    co: Vector;
    cp: Vector;
    eo: Vector;
    ep: Vector;

    constructor(co: number[],cp: number[],eo: number[],ep: number[]);
    constructor(co: Vector,cp: Vector,eo: Vector,ep: Vector) ;
    constructor(co : unknown,cp: unknown,eo: unknown,ep : unknown){ 
             
        if(co instanceof Vector && cp instanceof Vector && eo instanceof Vector && ep instanceof Vector){
            if(co.length != 8 || cp.length != 8  || eo.length != 12 || ep.length !=12 ) console.error("the number of corners should be 8,and the number of edges should be 12");
            this.co = co;
            this.cp = cp;
            this.eo = eo;
            this.ep = ep;
        }

        if(co instanceof Array && cp instanceof Array && eo instanceof Array && ep instanceof Array){
            if(co.length != 8 || cp.length != 8  || eo.length != 12 || ep.length !=12 ) console.error("the number of corners should be 8,and the number of edges should be 12");
            this.co = new Vector(co);
            this.cp = new Vector(cp);
            this.eo = new Vector(eo);
            this.ep = new Vector(ep);
        }
    }

    public applyMove(move : CubeState) : CubeState {
        let new_co : Vector = (this.co.apply_move(move.cp).add(move.co)).mod(3);
        let new_cp : Vector = this.cp.apply_move(move.cp);
        let new_eo : Vector = (this.eo.apply_move(move.ep).add(move.eo)).mod(2);
        let new_ep : Vector = this.ep.apply_move(move.ep);
        return new CubeState(new_co,new_cp,new_eo,new_ep);
    }
    //URF みたいな感じで指定したら 現在URFにある面の色を返してほしいな
    //RU みたいな感じで指定したら 現在 RUにある面の色を返してほしいな
    public getFaceOf(placeName : string) : string{
        if(placeName.length == 3){
            const cpIndex : number = this.cp.at(cornerIndex[placeName]); 
            const coIndex :number = this.co.at(cornerIndex[placeName]);
            console.log(cpIndex);
            console.log(coIndex);
            console.log(cornerNames[cpIndex])
            return cornerNames[cpIndex].charAt(coIndex);
        }
        if(placeName.length == 2){
            const epIndex : number = this.ep.at(edgeIndex[placeName]); 
            const eoIndex :number = this.eo.at(edgeIndex[placeName]);
            return cornerNames[epIndex].charAt(eoIndex);
        }
        console.log("invalid length of argument");
        return "";
    }

    public getFaceOfCorner(co : number,cp :number) : string {
        return cornerNames[cp].charAt(co);
    }

    public getFaceOfEdge(eo : number , ep : number) : string {
        return edgeNames[ep].charAt(eo);
    }
    //というか番号指定でやってそれに対応するやつを返せばいいのでは
}

const cornerNames  = {
    0: "ULB",
    1: "UBR",
    2: "URF",
    3: "UFL",
    4: "DBL",
    5: "DRB",
    6: "DFR",
    7: "DLF",
}
const edgeNames = {
    0:"BL",
    1:"BR",
    2:"FR",
    3:"FL",
    4:"UB",
    5:"UR",
    6:"UF",
    7:"UL",
    8:"DB",
    9:"DR",
    10:"DF",
    11:"DL",
}
const cornerIndex = {};
for(let i = 0;i < 8 ; i++){
    cornerIndex[cornerNames[i]] = i;
}
const edgeIndex = {};
for(let i = 0;i < 12 ; i++){
    edgeIndex[edgeNames[i]] = i;
}
